.button{
  box-shadow:none;
  -webkit-appearance:none;
  -moz-appearance:none;
  appearance:none;
  position:relative;
  vertical-align:top;
  display:inline-flex;
  height:2.75rem;
  justify-content:flex-start;
  cursor:pointer;
  text-align:center;
  align-items:center;
  white-space:nowrap;
  color:#636363;
  text-decoration:none;
  justify-content:center;
  padding:0 1.5rem;
  font-weight:600;
  border:1px solid #d7d8dc;
  border-radius:0.25rem;
  background-color:#fff
}

.button:active,.button:focus{
  outline:none
}

.button[disabled]{
  cursor:not-allowed
}

.button .icon:first-child:not(:last-child){
  margin-right:.5em;
  margin-left:-.8em
}

.button .icon:last-child:not(:first-child){
  margin-left:.5em;
  margin-right:-.8em
}

.button .icon:first-child:last-child{
  margin-left:-.8em;
  margin-right:-.8em
}

.button:hover{
  color:#000;
  border-color:#cccccc;
  background-color:#f7f7f7
}

.button.static{
  pointer-events:none;
  color:#afaeae;
  background-color:#fbfbfb
}

.button.static,.button[disabled]{
  border-color:#d7d8dc
}

.button[disabled]{
  opacity:.5;
  background-color:#fff
}

.button.loading{
  color:transparent;
  pointer-events:none
}

.button.loading:after{
  content:"";
  width:1rem;
  height:1rem;
  display:block;
  position:absolute;
  border-radius:100px;
  top:calc(50% - .5rem);
  left:calc(50% - .5rem);
  -webkit-animation:spin .5s linear infinite;
  animation:spin .5s linear infinite;
  border-color:transparent transparent #636363 #636363;
  border-style:solid;
  border-width:2px
}

.button.outlined{
  color:#d7d8dc
}

.button.outlined,.button.outlined:hover{
  background-color:transparent
}

.button.outlined.loading{
  color:transparent
}

.button.outlined.loading:after{
  border-left-color:#d7d8dc;
  border-bottom-color:#d7d8dc
}

.content{
