}

.content h1.subtitle+.title,.content h1.title+.subtitle,.content h2.subtitle+.title,.content h2.title+.subtitle,.content h3.subtitle+.title,.content h3.title+.subtitle,.content h4.subtitle+.title,.content h4.title+.subtitle,.content h5.subtitle+.title,.content h5.title+.subtitle,.content h6.subtitle+.title,.content h6.title+.subtitle{
