.checkbox{
  z-index:-1;
  width:1rem;
  height:1rem;
  opacity:.01;
  position:absolute;
  margin-top:.25rem
}

.checkbox+label{
  cursor:pointer;
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
  position:relative;
  color:#596679;
  line-height:1.5rem;
  display:inline-flex;
  padding-left:1.5rem
}

.checkbox+label:before{
  left:0;
  content:"";
  width:1rem;
  height:1rem;
  top:.25rem;
  position:absolute;
  border-radius:0.25rem;
  border:1px solid #d7d8dc;
  background-color:transparent
}

.checkbox+label:hover:before,.checkbox[type=checkbox]:checked+label:before{
  border-color:#cccccc;
  background-color:transparent
}

.checkbox[type=checkbox]:checked+label:after{
  content:"";
  left:.5rem;
  width:.3rem;
  height:.6rem;
  position:absolute;
  background-color:transparent;
  top:calc(.25rem + 2px);
  transform:translateX(-50%) rotate(45deg);
  border-right:2px solid #ff4738;
  border-bottom:2px solid #ff4738
}

.checkbox[type=checkbox]:checked+label:hover:before{
  border-color:#afaeae;
  background-color:transparent
}

.checkbox[type=checkbox]:checked+label:hover:after{
  border-right-color:#d43b2d;
  border-bottom-color:#d43b2d
}

.checkbox[type=checkbox]:focus+label:before{
  border-color:#afaeae;
  background-color:transparent
}

.checkbox[type=checkbox]:focus+label:after{
  border-right-color:#d43b2d;
  border-bottom-color:#d43b2d
}

.checkbox[type=radio]+label:before{
  border-radius:50%
}

.checkbox[type=radio]:checked+label:before{
  border:1px solid #d7d8dc;
  background-color:transparent
}

.checkbox[type=radio]:checked+label:after{
  content:"";
  left:.25rem;
  width:.5rem;
  height:.5rem;
  position:absolute;
  border-radius:999px;
  top:.5rem;
  background-color:#ff4738
}

.checkbox[type=radio]:checked+label:hover:after{
  border-color:#cccccc;
  background-color:transparent;
  background-color:#d43b2d
}

.checkbox[type=radio]:focus+label:before{
  border-color:#afaeae;
  background-color:transparent
}

.checkbox[type=radio]:focus+label:after{
  background-color:#d43b2d
}

